@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282d37;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #e4bb15;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root {
  --primary-white: #ffffff;
  --primary-light-grey: #f4f4f4;
  --primary-blue: rgb(110, 191, 235);
  --primary-dark: rgb(52,58,64);
  --primary-orange: rgb(221, 154, 53);
  --primary-grey: rgba(203, 216, 220, 0.844);
  --primary-black: #000000;
  --primary-red: #ef4035;



}

body {
  font-family: "Roboto Condensed" sans-serif !important;
}

/************************* Navbar ***************************/
nav {
  z-index: 2;
}

.logo {
  width: 11.00rem;
  height: 8.50rem;
}

/** Menu Links **/ 

nav a.nav-link {
  font-weight: 400;
  letter-spacing: 0.0350rem; 
  font-style: italic;
  font-style: oblique;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: .05rem;
  margin-left: 0.50rem;
  color: #ffffff !important;
  color: var(--primary-white) !important;
  border-top: 00.1875rem solid rgb(52,58,64);
  border-top: 00.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  margin-left: .75rem;
  color: #ffffff !important;
  color: var(--primary-white) !important;

}

@media(max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}



.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid rgb(221, 154, 53);
  border-top: 0.1875rem solid var(--primary-orange);

}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid rgb(221, 154, 53);
  border-top: 0.1875rem solid var(--primary-orange);
}

.nav-item {
  margin-left: 50px;
}



/************************* Header ***************************/

.header-wraper {
  position: relative;
  background: url(/react-portfolio/static/media/web-dev-5.e1f7ed6f.jpeg);
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
 
  
}

@media(max-width: 768px) {
  .main-info {
    font-size: 1.7rem !important;
  }
}



.main-info h1 {
  color: #ffffff;
  color: var(--primary-white);
  text-align: center;
  font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-style: italic;
  font-weight: bold;
}

@media(max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
}


/************************* Particles ***************************/


.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;

}

@media(max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

/************************* About Me ***************************/


.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;

}

.profile-img {
  width: 20rem;
  padding: .5rem;
  border: 1px solid rgb(221, 154, 53);
  border: 1px solid var(--primary-orange);

}

.about-heading {
  color: rgb(221, 154, 53);
  color: var(--primary-orange);
  text-transform: uppercase;
  z-index: 2;

}

@media(max-width: 468px) {
  .profile-img {
    width: 10rem;
  }
}

/************************* Interests ***************************/

.Interests {
  text-align: center;
  padding-bottom: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-grey);
}

.Interests h1 {
  color: rgb(52,58,64);
  color: var(--primary-dark);
  text-transform: uppercase;
 
}

.Interests .circle {
  position: relative;
  margin: .375rem auto;
  background: rgb(221, 154, 53);
  background: var(--primary-orange);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;

}

.Interests .icon {
  font-size: 1.4rem;
  color: #ffffff;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);


}

.Interests .box {
  height: 22rem;
  border-bottom: .3125rem solid rgb(221, 154, 53);
  border-bottom: .3125rem solid var(--primary-orange);
  padding: .625rem;
  margin-bottom: 1.875rem;
  background: #ffffff;
  background: var(--primary-white);
  transition: 0.4s ease-in-out;


}

.Interests .box:hover {
  background: rgb(221, 154, 53);
  background: var(--primary-orange);
  border-bottom: .3125rem solid rgb(52,58,64);
  border-bottom: .3125rem solid var(--primary-dark);

}

.Interests .box:hover .icon {
  color: rgb(52,58,64);
  color: var(--primary-dark);

}

.Interests .box:hover .circle {
  background: #ffffff;
  background: var(--primary-white);

}

.Interests .box:hover p {
  color: #ffffff;
  color: var(--primary-white);

}

/************************* Portfolio ***************************/

.portfolio-wrapper {
  background: #f4f4f4;
  background: var(--primary-light-grey);
  padding: 4rem 0;

}

.portfolio-image {
  width: 15rem;
  border: 1px solid rgb(221, 154, 53);
  border: 1px solid var(--primary-orange);
  padding: 0 0.5rem;
}

.portfolio-image-box {
  flex: 20% 1;
  margin: 1rem;
}




